<template>

  <div ref="metaMsg" class='metaMsg'>

    <div class="pocketnet_iframe" v-if="iframe" ref="iframe">
      
    </div>

    <div class="pocketnet" v-else>

      <div class="header">
        <img class="userpic" :src="image" alt="Profile picture">
        <div class="header-text">
          <span class="title">{{ title }}</span>
          <span v-if="getUrl" class="pocketnet">Pocketnet.app</span>
        </div>
      </div>

      <div class="description">
        <span>{{ description }}</span>
      </div>
      <div 
          v-if="!video" 
          class="image"
          :style="{width: imageWidth + 'px', height: imageHeight + 'px'}"
        >
      <img :src="image" alt="meta Image" v-if="image">
    </div>
      <!-- <div v-if="video" class="video">


      </div> -->
        <Player v-if="video" :video_url="video_url" :autoplay="true" />

    </div>
  </div>
</template>
<script>
import Player from '@/components/chat/player/Player.vue'
import Widgets from '@/application/utils/widgets.js'




import f from '@/application/functions'
import { createLogger } from 'node_modules/vuex/types'
// import Plyr from 'plyr'

export default {
  components: {
    Player
  },

  props: {
    title: String,
    msg: String,
    name: String,
    description: String,
    image: String,
    video: String,
    metaUrl: String,
    h: Number, 
    w: Number,
    initImageWidth: Number,
    imageFactor: Number,
    clientWidth: Number,
    post: String
  },

  data: () => {
    return {
      link: '',
      txt: String,
      iframe: null,
    }
  },
  computed: {

    imageWidth(){

      return this.clientWidth < 650 ? this.imageFactor * this.initImageWidth : this.initImageWidth;
    },
      getUrl : function () {
        // if(this.metaUrl) return new URL(this.metaUrl).hostname === 'pocketnet.app'
    },

    imageHeight(){

      const ratio = this.w / this.imageWidth;

      return this.h / ratio || 300;
    }
  },

  beforeMount() {
    // console.log('META_MESSAGE', this.video)
    if(this.video) this.video_url = f.videoUrlParser(this.video)

  },
  mounted() {

    let url = new URL(this.$props.metaUrl)

    if(url.host === 'pocketnet.app') {
      this.iframe = true

      let widget = new Widgets()

      setTimeout(() => {
        widget.makefromurl(this.$refs.iframe, this.$props.metaUrl)
      })
      console.log('@@@ 11', this.iframe)
    }
  },
  methods: {

  },

}
</script>
<style lang="sass" scoped>
.video
  .wrap 
    width: 100% !important
  
.metaMsgPn
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px
  -webkit-box-shadow:: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px
  -moz-box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px
  width: 100%
  border-radius: 1em
  padding: .5em

.metaMsg
  border: 1px solid $color-bg-second
  width: 100%
  border-radius: 1em
  padding: 0
  height: auto
  overflow: hidden

  .header
    display: flex
    margin-bottom: 15px
    .header-text
      flex-direction: column
      .title
        margin: 0
    .pocketnet
      font-size: 11px
      font-weight: 500
      color: gray

  .userpic
    width: 50px
    height: 50px
    border-radius: 30px

  .textMsg
    word-break: break-all
    font-size: 0.8em
    cursor: pointer
    &:hover
     a
       display: block
       width: 100%
       height: 100%
       color: #62bcf9

    a
      font-weight: bold
      color: #0059ff

  .title
    font-weight: bold
    display: block
    font-size: 0.9em
    margin: 0.4em 0
    span
      display: block

  .description
    margin: 0.2em 0
    font-size: 0.8em
    display: flex
    justify-content: flex-start

  .image
    margin: 0.2em 0

    img
      display: block
      object-fit: contain
      border-radius: .5em
      width: 100%
      object-fit: cover
  .video
    margin: .5em 0
    iframe
      border: 0
      border-radius: .5em

</style>